  margin: 0 auto;
  padding: 20px;
}

/* Side panel styles */
.side-panel {
  flex: 1 1;
  padding: 20px;
  background-color: #f0f0f0;
  transition: width 0.3s ease;
}

.profile img {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  margin-bottom: 20px;
}

.section-titles h4 {
  cursor: pointer;
  margin-bottom: 10px;
  padding: 10px;
}

.section-titles h4:hover {
  color: red;
  background: white;
}

/* Main content styles */
.main-content {
  flex: 3 1;
  padding: 20px;
  background-color: #fff;
}

.header h1 {
  text-align: center;
  margin-bottom: 20px;
}

.dashboard-section {
  margin-bottom: 30px;
}

/* Responsive styles */
@media only screen and (max-width: 768px) {
  .trader-dashboard {
    flex-direction: row;
  }

  .side-panel,
  .main-content {
    width: 100%;
    padding: 10px;
  }

  .profile img {
    width: 80px;
    height: 80px;
  }

  .section-titles h4 {
    font-size: 16px;
    padding: 8px;
  }

  
}

