/* login.css */

body {
	font-family: Arial, sans-serif;
	background-color: #f4f4f4;
	margin: 0;
	display: flex;
	justify-content: center;
	align-items: center;
	height: 100vh;
}

h2 {
	text-align: center;
	color: #333;
	margin-bottom: 16px;
}

form {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	max-width: 300px;
	margin: 0 auto;
	padding: 10px 0;
}

label {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
}

input {
	padding: 8px;
	margin-bottom: 16px;
	border: 1px solid #ccc;
	border-radius: 4px;
}

button {
	background: red;
	width: auto;
	font-size: 18px;
	color: #fff;
	padding: 8px 12px;
	border: 0;
	border-radius: 10px;
	cursor: pointer;
}

button:hover {
	background-color: #0056b3;
}

.register-button {
	background-color: #28a745;
	color: #fff;
	padding: 8px;
	border: none;
	border-radius: 4px;
	cursor: pointer;
}

.register-button:hover {
	background-color: #218838;
}

.register-link {
	color: blue;
	text-decoration: none;
}

.register-link:hover {
	text-decoration: underline;
	color: red;
}

@media (max-width: 700px) {
	.container {
		width: 100%;
		padding: 0;
	}
	form {
		width: 100%;
	}
	h2 {
		font-size: 24px;
	}
	.footer {
		position: fixed;
		left: 0;
		bottom: 0;
		width: 100%;
		background-color: red;
		color: white;
		text-align: center;
	}
}
