/* leaderboard.css */
body {
	margin: 0;
	padding: 0;
	height: 100vh;
}

.leaderboard-container {
  max-width: 1000px;
  margin: 0 auto;
  padding: 20px;
}

.leaderboard-header {
  text-align: center;
  margin-bottom: 20px;
}

.leaderboard-table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 20px;
}

.leaderboard-table th,
.leaderboard-table td {
  border: 1px solid #ddd;
  padding: 10px;
  text-align: center;
}

.leaderboard-table th {
  background-color: #f2f2f2;
}

.bottom-icons-container {
  display: flex;
  justify-content: space-between;

}

.icon {
	margin-right: 20px;
}

.icon img {
  width: 50px;
  height: 50px;
  margin: 5px 20px;
}
